@charset "UTF-8";
html, body, [canvas=container], [off-canvas] {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: #263238;
  letter-spacing: -0.3px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  position: relative;
  background: #F5F5F5;
}

.login-background-wrapper {
  height: 100%;
  width: 100%;
}

.woocommerce-page .woocommerce {
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  width: 100%;
}

@media (min-width: 768px) {
  .login-background-wrapper {
    min-height: 600px;
  }
  .login-background {
    background-image: url("../images/login-background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }
}

.login {
  background: #ffffff;
}

.login h1 {
  position: relative;
  width: 200px;
  height: 37px;
  display: inline-block;
  background-size: 200px 37px;
  background-repeat: no-repeat;
  background-image: url(../images/jb-logo.png);
  background-position: left;
}

.login h1 a {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 37px;
  text-indent: -9999px;
  outline: 0;
  overflow: hidden;
}

.account-login {
  background-image: none !important;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  content: " ";
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  background-color: #F44336;
  height: 100%;
}

.account-login h1 a {
  margin-bottom: 30px;
  width: 200px;
  height: 37px;
  background-size: 200px 37px;
  margin-left: auto;
  margin-right: auto;
  background-repeat: no-repeat;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.3em;
  margin: 0 auto 25px;
  padding: 0;
  text-decoration: none;
  text-indent: -9999px;
  outline: 0;
  overflow: hidden;
  display: block;
  background-image: url(../images/jb-logo.png);
  background-position: center center;
}

.account-login .login-left {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  margin: 0;
  max-width: 580px;
  padding: 40px 0 0;
  background-color: #ffffff;
}

.account-login .tabs {
  max-width: 420px;
  margin: 0 auto !important;
  padding-left: 25px;
  padding-right: 25px;
}

.account-login form.login, .account-login form.register {
  border: none !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
  padding-bottom: 0 !important;
  margin: 0 !important;
}

@media (min-width: 576px) {
  .account-login .login-left {
    width: 85%;
  }
  .account-login .login-right {
    background-attachment: initial !important;
    bottom: 0;
    height: 100%;
    left: 580px;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    width: calc(100% - 580px);
    z-index: 0;
    background-image: url(../images/login-background.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }
}

@media (min-width: 576px) {
  .login-image {
    background-image: url(../images/login-background.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
  }
}

@media only screen and (min-width: 800px) {
  .account-login .login-left {
    padding: 100px 0 0;
  }
}

.woocommerce-account header, .woocommerce-account footer, .woocommerce-account .brand-strip {
  display: none;
}

.woocommerce-account main, .woocommerce-account .woocommerce {
  height: 100%;
}

.logged-in.woocommerce-account header, .logged-in.woocommerce-account footer, .logged-in.woocommerce-account .brand-strip {
  display: block;
}

.logged-in.woocommerce-account main, .logged-in.woocommerce-account .woocommerce {
  height: auto;
}

header .btn-outline-light {
  border-color: rgba(255, 255, 255, 0);
}

header .btn-outline-light:hover, header .btn-outline-light:focus {
  border-color: white;
  background-color: transparent;
  color: #ffffff;
}

header .btn-outline-light:not(:disabled):not(.disabled).active, header .btn-outline-light:not(:disabled):not(.disabled):active, header .show > .btn-outline-light.dropdown-toggle {
  color: #ffffff;
  background-color: transparent;
  border-color: #ffffff;
}

header .btn-outline-dark {
  border-color: rgba(38, 50, 56, 0);
}

header .btn-outline-dark:hover, header .btn-outline-dark:focus {
  border-color: #263238;
  background-color: transparent;
  color: #263238;
}

header .btn-outline-dark:not(:disabled):not(.disabled).active, header .btn-outline-dark:not(:disabled):not(.disabled):active, header .show > .btn-outline-dark.dropdown-toggle {
  color: #263238;
  background-color: transparent;
  border-color: #263238;
}

.brand-strip {
  height: 4px;
  width: 100%;
}

.dropdown-menu {
  padding: .5rem 0 !important;
  margin: 0;
  color: #263238;
  background-color: #ffffff;
  border: none;
  border-radius: 0;
  box-shadow: 0 3px 15px rgba(38, 50, 56, 0.15);
}

.dropdown-menu .dropdown-header {
  color: #757575;
  text-transform: uppercase;
}

.dropdown-menu .dropdown-item a {
  color: #263238;
  font-size: .875rem;
  display: block;
}

.dropdown-menu .dropdown-item:active {
  background-color: #E3F2FD;
}

footer * {
  color: #ffffff;
}

footer {
  background-color: #212121;
}

footer .seal {
  height: 150px;
  width: 150px;
  background-image: url("../images/100.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

footer .social i {
  font-size: 1.5rem;
}

footer a:hover, footer a:hover i {
  color: #64B5F6;
}

